import { useLocation, Link } from 'react-router-dom';

import Dummy from "../data/menu.json";

export default function Header() {

    const location = useLocation();
    const isHome = (location.pathname === '/');

    const isActive = (name) => {
        const path = location.pathname;
    
        if (path === '/' && name === 'profile') return true;
        return path === `/${name}` || path.startsWith(`/${name}/`);
    };

    
    return(
        <header>
            <h1 className="blind"> </h1>
            <nav>
                <ul>
                    {Dummy.map((item) => (
                        <li key={item.order} className={isActive(item.name) ? 'on' : undefined}>
                            <Link to={`/${item.name}`}>{item.title}</Link>
                        </li>
                    ))}
                </ul>
            </nav>
        </header>
    );
}
