import { useNavigate, useLocation, Link } from 'react-router-dom';

export default function Profile() { 

    const location = useLocation();
    const navigate = useNavigate();

    const compList = [
        {
            id:"Megastudy", 
            param : "mega"
        }, 
        {
            id:"4-Depth", 
            param : "depth"
        }
    ]

    const handleClick = (param) => {
        navigate(`/portlist/${param}`);
    };

    const isHome = (location.pathname === '/');


    return(
        <>
            <div className="case_link">
                {!(isHome || location.pathname === "/profile") &&
                    <Link to="/profile" title=' '>
                        <span>Profile</span>
                        <span className="img"><img src={`${process.env.PUBLIC_URL}/images/icon/me.jpg`} alt="me" /></span>
                    </Link>
                    
                }
                {(isHome || location.pathname === "/profile") &&
                    <Link to="/portlist" title=' '>
                        <span>Portfolio</span>
                        <span className="img"><img src={`${process.env.PUBLIC_URL}/images/icon/me.jpg`} alt="me" /></span>
                    </Link>
                    
                }
            </div>

            {!(isHome || location.pathname === "/profile") &&
                <ul className="compList_wrap">
                    {compList.map((item) => (
                        <li key={item.id}>
                            <a onClick={() => handleClick(item.param)} style={{ cursor: "pointer" }}>
                                {item.id}
                            </a>
                        </li>
                    ))}
                </ul>
            }
        </>


    )


}