
import React from 'react';
import { Route, Routes } from "react-router-dom";
import Header from './components/Header';
import Footer from './components/Footer';
import Profile from './components/Profile';
import WorklistMg from './components/Worklist_mg';
import WorklistFd from './components/Worklist_fd';
import Case from './components/Case';
import "./reset.css"
import './App.scss';



export default function App() {
    return (
        <div className="allAbout_wrap">
            <Header />
            <article>
                <section>
                    <Case />
                        <Routes >
                            <Route index element={<Profile />} /> 
                            <Route path="profile" element={<Profile />} />
                            <Route path="portlist" element={<WorklistMg />} />
                            <Route path="portlist/mega" element={<WorklistMg />} />
                            <Route path="portlist/depth" element={<WorklistFd />} />
                        </Routes>
                </section>
            </article>
            <Footer />
        </div>
    );
}
